import debug from '../components/debug/debug'; // 
import fixed from '../components/fixed/fixed'; //  
import Avatar from '../components/avatar/avatar'; // 
import lighter from '../components/code-lighter/code-lighter'; // 
import '../components/parallax/jquery.scrolly.js'; // 


export default class Components {
  constructor(setNavActive) {
    // 
    setNavActive(2);
    //  
    debug('components controller is load');
    //  
    fixed();
  }
  // 
  index() {
    // 
    this.gaoliang();
    // 
    this.avatarDemo();
    // swiper
    this.swiperDemo();
    // 
    this.parallaxDemo();
  }
  // 
  gaoliang() {
    const option = {
      tabSpace: 0,
      style: 'light'
    };
    lighter.auto(option);
  }
  // 
  avatarDemo() {
    const avatar = new Avatar();
    avatar.index();
  }
  // swiper
  swiperDemo() {
    const swiper = new Swiper('.swiper-demo', {
      pagination: '.swiper-demo .swiper-pagination',
      paginationClickable: true,
      nextButton: '.swiper-demo .swiper-button-next',
      prevButton: '.swiper-demo .swiper-button-prev',
      parallax: true,
      speed: 2000,
      autoplay: 2000
    });
  }
  // 
  parallaxDemo() {
    $('.parallax').scrolly({
      bgParallax: true
    });
  }
}
